* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
  text-align: center;
  font-family: Roboto, Arial, sans-serif;
}

body {
  background-color: black;
  width: 100%;
  height: 100vh;
}

.grid {
  display: grid;
  height: 100vh;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;

}

.backgroundImage {
  grid-area: 1 / 1 / 1 / 1;
  z-index: -3;
}

.backgroundImage img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  filter: opacity(30%);
}

.container {
  grid-area: 1 / 1 / 1 / 1;
  width: 80%;
  height: 60vh;
  border-radius: 15px;
  margin: 20vh auto;
  background-color: white;
}

.container h1 {
  padding: 50px;
}

.container button{
  padding: 20px 30px;
  font-size: 2em;
  font-weight: bolder;
  margin-bottom: 30px;
  border: 0px;
  /* background-color: red; */
}

.container h2{
  font-size: 1.9em;
}

@media only screen and (min-width: 600px) {
  .container {
    width: 70%;
    height: 50vh;
    margin: 20vh auto;
  }
}

@media only screen and (min-width: 1200px) {
  .container {
    width: 800px;
    height: 50vh;
    margin: 20vh auto;
  }
}

#map {
  width: 500px;
  height: 500px;
}